import React, { Suspense, useState } from 'react';
import { Routes, Route } from 'react-router-dom';
import Loader from './components/Misc/Loader';
import { RootStateType } from './redux/store';
import { useSelector } from 'react-redux';

// Define route object type
interface RouteConfig {
  path: string;
  label: string;
  Component: React.LazyExoticComponent<React.FC>;
}

// Define the routes array
const routes: RouteConfig[] = [
  { path: '/', label: 'Home', Component: React.lazy(() => import('./pages/Profile')) },
  { path: '/about', label: 'About', Component: React.lazy(() => import('./pages/About')) },
  { path: '/skills', label: 'Skills', Component: React.lazy(() => import('./pages/Skills')) },
  { path: '/education', label: 'Education', Component: React.lazy(() => import('./pages/Education')) },
  { path: '/experience', label: 'Experience', Component: React.lazy(() => import('./pages/Experience')) },
  { path: '/projects', label: 'Projects', Component: React.lazy(() => import('./pages/Projects')) },
];

// Define quick links for the header
const quickLinks = [
  { label: 'Home', url: '/' },
  { label: 'About', url: '/about' },
  { label: 'Skills', url: '/skills' },
  { label: 'Education', url: '/education' },
  { label: 'Experience', url: '/experience' },
  { label: 'Projects', url: '/projects' },
];

const Header = React.lazy(() => import('maddy_widget/Header'));
const Footer = React.lazy(() => import("maddy_widget/Footer"));
const WhatsAppWidget = React.lazy(() => import("maddy_widget/WhatsAppWidget"));

const AppRoutes: React.FC = () => {
  const { location, siteTitle, socialLinks } = useSelector((state: RootStateType) => state.contactDetailsSlice);
  const services = useSelector((state: RootStateType) => state.servicesSlice.services);
  const technologies = useSelector((state: RootStateType) => state.technologySlice.technologies);
  const [menuOpen, setMenuOpen] = useState(false);
  return (
    <>
      <Suspense fallback={<Loader text="Loading..." fullScreen={true} />}>
      <Header
        menuLinks={quickLinks}
        logoSrc="mainLogo"
        menuOpen={menuOpen}
        setMenuOpen={setMenuOpen}
        onLoginClick={() => console.log('Login Clicked')}
      />
      
      <Routes>
          {routes.map(({ path, Component }) => (
            <Route key={path} path={path} element={<Component />} />
          ))}
        </Routes>

        <Footer
        quickLinks={quickLinks}
        socialLinks={socialLinks}
        services={services}
        technologies={technologies}
        logoSrc={"mainLogo"}
        companyName={siteTitle}
        address={location.address}
        themeColors={{
          background: "#333333",
          text: "#ffffff",
          accent: "#FFD700",
          linkHover: "#FFA500",
        }}
      />

      {/* WhatsApp Widget */}
      <WhatsAppWidget
        phoneNumber="8886380746"
        position={{ bottom: 50, right: 30 }}
        backgroundColor="#25D366"
        iconColor="white"
        iconSize={35}
        tooltipText="Contact us on WhatsApp"
        draggable={true}
      />
      </Suspense>
    </>
  );
};

export default AppRoutes;
